.PopUp_container {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.PopUp_container .PopUp_container_box {
  width: 60%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.PopUp_container .PopUp_container_box .box {
  display: flex;
  width: 80%;
  margin-top: -20px;
}

.PopUp_container .PopUp_container_box .box .btn {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0.6rem 1rem;
  cursor: pointer;
}

.PopUp_container .PopUp_container_box .components {
  width: 100%;
}
